@use "utilities" as *;
@use "globals" as *;

.recipe {
  display: none;
  margin: rem(16) auto;
  margin-bottom: rem(12);
  border-radius: rem(30);
  overflow-y: scroll;
  height: 55vh;
  min-width: rem(222);
  max-width: rem(298);
  width: 70vw;
  border: solid 2px $color-primary;
  // Fix visible scroll outside border-radius in safari
  transform: translateZ(0);

  @include breakpoint(medium) {
    margin: 0 auto;
    width: 100%;
    max-width: none;
    height: auto;
  }

  &__fig {
    width: 100%;
    height: 23vh;
    min-height: rem(150);
    position: relative;
    border-top-left-radius: rem(30);
    border-top-right-radius: rem(30);
    overflow: hidden;
    -webkit-clip-path: content-box;
    clip-path: content-box;

    @include breakpoint(medium) {
      max-height: rem(200);
    }
  }

  &__img {
    width: 100%;
    display: block;
    height: 100%;
    object-fit: cover;
  }

  &__title {
    position: absolute;
    width: 100%;
    background: rgb(255, 249, 231, 0.6);
    padding: rem(8);
    bottom: 0;
    left: 0;
    font-size: rem(24);
    overflow: ellipsis;
  }

  &__user-generated {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    line-height: rem(24);
    margin-left: rem(5);
    letter-spacing: 0;
    width: rem(28);
    height: rem(28);
    background: $color-primary-light;
    border-radius: 50%;
    font-size: rem(18);
  }

  &__details {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: rem(12) auto;
  }

  &__info {
    font-size: rem(20);
    margin-top: rem(8);

    &:nth-child(2) {
      margin-top: rem(6);
    }
    &-buttons {
      display: inline-flex;
      margin-left: rem(5);
      gap: rem(10);
    }
  }

  .heading--2 {
    text-align: center;
    text-decoration: underline;
    font-size: rem(24);
  }

  &__ingredients,
  &__directions {
    margin: rem(24) rem(18);
  }

  &__ingredient-list {
    font-size: rem(18);
    margin: rem(24) auto;
    max-width: rem(240);
  }

  &__ingredient {
    margin: rem(14) auto;
    display: grid;
    grid-template-columns: 1fr 2fr 7fr 2fr;
    // justify-items: center;
  }

  &__quantity {
    justify-self: center;
  }

  &__directions-text {
    text-align: center;
    font-size: rem(18);
    margin: rem(16) auto;
  }
}
